@import url("https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap");
body {
  width: 100%;
  height: 100vh;
}

* {
  margin: 0;
  padding: 0;
  background-color: #ecf4e5;
  font-family: ubuntu;
}

header h1 {
  padding: 10px;
  text-align: center;
  background-color: #b5c18e;
  color: #153448;
  margin-bottom: 10px;
}

.view-btns {
  padding: 10px;
  display: flex;
  justify-content: center;
  gap: 30px;
}

button {
  padding: 5px;
  border-radius: 5px;
  font-size: medium;
  background-color: #2e8b57;
  color: white;
}

.add-agenda {
  width: 700px;
  margin: 20px auto;
}

form {
  display: flex;
  width: 100%;
  flex-direction: column;
  margin-bottom: 20px;
}

form > input {
  width: calc(100% - 13.2px);
  padding: 5px;
}

label {
  margin: 10px 0;
}

.add-agenda.btn {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.error {
  color: red;
}

.agendas {
  width: 700px;
  margin: 10px auto;
}

.agenda {
  width: 100%;
  border-radius: 10px;
  border: 2px solid grey;
  margin: 20px 0;
}

.agenda * {
  padding: 5px 5px;
  margin: 0px 5px;
}

@media (max-width: 700px) {
  .add-agenda,
  .agendas {
    width: 100%;
  }
}
